import React from 'react'
import { SEO } from '../components/SEO'
import Footer from '../components/Footer'
import Header from '../components/Header'
import Layout from '../components/layout/Layout'
import SectionLayout from '../components/layout/SectionLayout'
import { StaticImage } from 'gatsby-plugin-image'
import { AnchorLink } from 'gatsby-plugin-anchor-links'

const about = () => {
  return (
    <Layout>
      <Header />
      <SectionLayout>
        {/* <!-- About Section --> */}
        <section className='mx-auto max-w-4xl'>
          <div className='mx-auto py-12 px-4 sm:px-6 lg:px-8'>
            <div className='mx-auto max-w-3xl text-center'>
              <h2 className='text-3xl font-bold tracking-tight text-orange-400 dark:text-slate-400 sm:text-4xl'>
                About Me!
              </h2>
              {/* <p className='mt-4 text-gray-500'>
                As a digital creative, your laptop or tablet is at the center of
                your work. Keep your device safe with a fabric sleeve that
                matches in quality and looks.
              </p> */}
            </div>

            <div className='mt-16 space-y-16'>
              {/* Odd block 1 */}
              <div className='flex flex-col gap-y-8 lg:grid lg:grid-cols-12 lg:items-center lg:gap-x-8'>
                <div className='mt-6 flex-col lg:col-span-6 lg:col-start-1 lg:row-start-1 lg:mt-0 xl:col-span-6'>
                  <h3 className='text-lg md:text-xl font-bold text-orange-400 dark:text-slate-400'>
                    Who I am?
                  </h3>
                  <p className='mt-2 text-base md:text-lg text-gray-500'>
                    I'm Ankeet Patel, a multifaceted professional with a
                    background in training, content development, eLearning and
                    instructional design. With over a decade of experience, I've
                    created a range of performance-oriented learning experiences
                    for learners in the educational, vocational, and
                    professional learning contexts. These experiences took
                    several forms: eLearning packages hosted on LMS, job aids,
                    web-based ILT, and self-paced video-based training, among
                    others.
                  </p>
                </div>
                <div className='flex flex-col lg:col-span-6 lg:col-start-7 lg:row-start-1 xl:col-span-6 xl:col-start-7'>
                  <div className='overflow-hidden rounded-lg bg-gray-100'>
                    <StaticImage
                      src='../images/assets/profile_images/about_us_top.png'
                      alt='Profile picture of Ankeet Patel'
                      className='object-cover object-center'
                    />
                  </div>
                </div>
              </div>
              {/* Even block 2 */}
              <div className='flex flex-col gap-y-8 lg:grid lg:grid-cols-12 lg:items-center lg:gap-x-8'>
                <div className='mt-6 lg:col-span-6 lg:col-start-7 lg:row-start-1 lg:mt-0 xl:col-span-6 xl:col-start-7'>
                  <h3 className='text-lg md:text-xl font-bold text-orange-400 dark:text-slate-400'>
                    What I offer?
                  </h3>
                  <p className='mt-2 text-base md:text-lg text-gray-500'>
                    A lifelong learner, I am devoted to continually honing my
                    skills. My sweet spot is the intersection of art and science
                    involved in the learning design process. I always put people
                    at the center and strive to create supportive, collaborative
                    work environments with peers, stakeholders and subject
                    matter experts (SMEs) as we work toward reaching common
                    goals.
                  </p>
                </div>
                <div className='flex-auto lg:col-span-6 lg:col-start-1 lg:row-start-1 xl:col-span-6'>
                  <div className='overflow-hidden rounded-lg'>
                    <StaticImage
                      src='../images/assets/about_us/what_i_can_offer.png'
                      alt='What I can offer'
                      className='object-cover object-center'
                    />
                  </div>
                </div>
              </div>
              {/* Odd block 3 */}
              <div className='flex flex-col gap-y-8 lg:grid lg:grid-cols-12 lg:items-center lg:gap-x-8'>
                <div className='mt-6 flex-col lg:col-span-6 lg:col-start-1 lg:row-start-1 lg:mt-0 xl:col-span-6'>
                  <h3 className='text-lg md:text-xl font-bold text-orange-400 dark:text-slate-400'>
                    How I operate?
                  </h3>
                  <p className='mt-2 text-base md:text-lg text-gray-500'>
                    Analyzing the learning context, the performance goal, and
                    the learners is a cornerstone of my work. For corporate
                    learning interventions, I frequently use action mapping,
                    design thinking and Gagne's Nine Events to fill knowledge
                    and skill gaps. For vocational and academic engagements,
                    you'll find me dabbling in more traditional models, such as
                    ADDIE and SAM. I also use Kirkpatrick's Model for evaluating
                    the effectiveness of learning engagements.
                  </p>
                </div>
                <div className='flex flex-col lg:col-span-6 lg:col-start-7 lg:row-start-1 xl:col-span-6 xl:col-start-7'>
                  <div className='overflow-hidden rounded-lg bg-gray-100'>
                    <StaticImage
                      src='../images/assets/about_us/how_i_operate.png'
                      alt='How I operate'
                      className='object-cover object-center'
                    />
                  </div>
                </div>
              </div>
              {/* Even block 4 */}
              <div className='flex flex-col gap-y-8 lg:grid lg:grid-cols-12 lg:items-center lg:gap-x-8'>
                <div className='mt-6 lg:col-span-6 lg:col-start-7 lg:row-start-1 lg:mt-0 xl:col-span-6 xl:col-start-7'>
                  <h3 className='text-lg md:text-xl font-bold text-orange-400 dark:text-slate-400'>
                    What I am looking for?
                  </h3>
                  <p className='mt-2 text-base md:text-lg text-gray-500'>
                    I am all about growth and results, seeking to widen my
                    professional network and build mutually beneficial
                    partnerships. If you or your organization would like to
                    leverage modern learning experiences that help people reach
                    their full potential, I am confident that I can be an
                    important cog in that wheel. Don't hesitate to contact me if
                    you think I can help!
                  </p>
                </div>
                <div className='flex-auto lg:col-span-6 lg:col-start-1 lg:row-start-1 xl:col-span-6'>
                  <div className='overflow-hidden rounded-lg bg-gray-100'>
                    <StaticImage
                      src='../images/assets/about_us/what_looking_for.png'
                      alt='What I am looking for'
                      className='object-cover object-center'
                    />
                  </div>
                </div>
              </div>
              {/* Odd block 5 */}
              <div className='flex flex-col gap-y-8 lg:grid lg:grid-cols-12 lg:items-center lg:gap-x-8'>
                <div className='mt-6 flex-col lg:col-span-6 lg:col-start-1 lg:row-start-1 lg:mt-0 xl:col-span-6'>
                  <h3 className='text-lg md:text-xl font-bold text-orange-400 dark:text-slate-400'>
                    What helps me unwind?
                  </h3>
                  <p className='mt-2 text-base md:text-lg text-gray-500'>
                    Well, I am a huge fan of racquet sports. So, when I'm not
                    collaborating with management / department heads to conduct
                    needs analysis or converting a storyboard into a
                    high-fidelity eLearning prototype, you'll find me sweating
                    it off on a badminton court or enjoying Tennis grand slams.
                    I also take great pleasure in leafing through technology
                    blogs and trying out new applications (even if these have
                    nothing to do with my work).
                  </p>
                </div>
                <div className='flex flex-col lg:col-span-6 lg:col-start-7 lg:row-start-1 xl:col-span-6 xl:col-start-7'>
                  <div className='overflow-hidden rounded-lg bg-gray-100'>
                    <StaticImage
                      src='../images/assets/about_us/what_helps_unwind.png'
                      alt='What helps me unwind'
                      className='object-cover object-center'
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class='flex justify-center mb-8'>
            <button class='inline-flex rounded border-0 bg-orange-200 py-2 px-6 text-lg font-medium text-slate-700 hover:bg-orange-300 focus:outline-none shadow-md'>
              <AnchorLink to='/#projects' title='My Projects'>
                See Recent Projects
              </AnchorLink>
            </button>
          </div>
        </section>
        <hr className='mx-auto w-1/2 bg-black dark:bg-white' />
        <section className='mx-auto my-12 max-w-4xl scroll-mt-32 p-6'>
          <div>
            <div className=''>
              <ul className=''>
                <li>
                  <div className='space-y-4 sm:grid sm:grid-cols-3 sm:items-start sm:gap-6 sm:space-y-0'>
                    <div className='w-60 mx-auto'>
                      <StaticImage
                        className='rounded-lg object-cover shadow-lg'
                        src='../images/assets/profile_images/contact_me.png'
                        alt="Ankeet Patel's profile picture"
                      />
                    </div>
                    <div className='sm:col-span-2'>
                      <div className='space-y-4'>
                        <div className='space-y-1 text-lg font-medium leading-6'>
                          <h2 className='text-3xl font-bold tracking-tight text-orange-400 dark:text-slate-400 sm:text-4xl mb-4'>
                            Connect With Me!
                          </h2>
                          <h3>Ankeet Patel</h3>
                          <p className='text-orange-400 dark:text-slate-400'>
                            Trainer | Content Developer | eLearning Expert |
                            Instructional Designer
                          </p>
                        </div>
                        <div className='text-lg'>
                          <p className='text-gray-500'>
                            If you are looking to design meaningful, memorable
                            and impactful learning experiences and training
                            programs, don't hesitate to contact me!
                          </p>
                        </div>
                        <ul className='flex space-x-5'>
                          <li>
                            <a
                              href='https://www.linkedin.com/in/ankeetpatel/'
                              className='text-gray-400 hover:text-gray-500'
                            >
                              <span className='sr-only'>LinkedIn</span>
                              <svg
                                className='h-7 w-7'
                                fill='currentColor'
                                viewBox='0 0 20 20'
                                aria-hidden='true'
                              >
                                <path d='M0 1.146C0 .513.526 0 1.175 0h13.65C15.474 0 16 .513 16 1.146v13.708c0 .633-.526 1.146-1.175 1.146H1.175C.526 16 0 15.487 0 14.854V1.146zm4.943 12.248V6.169H2.542v7.225h2.401zm-1.2-8.212c.837 0 1.358-.554 1.358-1.248-.015-.709-.52-1.248-1.342-1.248-.822 0-1.359.54-1.359 1.248 0 .694.521 1.248 1.327 1.248h.016zm4.908 8.212V9.359c0-.216.016-.432.08-.586.173-.431.568-.878 1.232-.878.869 0 1.216.662 1.216 1.634v3.865h2.401V9.25c0-2.22-1.184-3.252-2.764-3.252-1.274 0-1.845.7-2.165 1.193v.025h-.016a5.54 5.54 0 0 1 .016-.025V6.169h-2.4c.03.678 0 7.225 0 7.225h2.4z' />
                              </svg>
                            </a>
                          </li>
                          <li>
                            <a
                              href='mailto:ankeet85@gmail.com?subject=Hey Ankeet!'
                              className='text-gray-400 hover:text-gray-500'
                            >
                              <span className='sr-only'>Email</span>
                              <svg
                                className='h-7 w-7'
                                fill='currentColor'
                                viewBox='0 0 20 20'
                                aria-hidden='true'
                              >
                                <path d='M8.941.435a2 2 0 0 0-1.882 0l-6 3.2A2 2 0 0 0 0 5.4v.314l6.709 3.932L8 8.928l1.291.718L16 5.714V5.4a2 2 0 0 0-1.059-1.765l-6-3.2ZM16 6.873l-5.693 3.337L16 13.372v-6.5Zm-.059 7.611L8 10.072.059 14.484A2 2 0 0 0 2 16h12a2 2 0 0 0 1.941-1.516ZM0 13.373l5.693-3.163L0 6.873v6.5Z' />
                              </svg>
                            </a>
                          </li>
                        </ul>
                      </div>
                    </div>
                  </div>
                </li>
              </ul>
            </div>
          </div>
        </section>
        <hr className='mx-auto w-1/2 bg-black dark:bg-white' />
      </SectionLayout>
      <Footer />
    </Layout>
  )
}

export default about

export const Head = () => (
  <SEO title='Ankeet Patel - Professional Background | Instructional Design | Contact Details' />
)
